@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
body {
  color: #fff;
  background: linear-gradient(45deg, #08041d, #200852) fixed;
  align-items: center;
  font-family: Roboto, sans-serif;
  position: relative;
}

h1, h2, p, ul {
  margin: 0;
  padding: 0;
}

ul {
  font-size: 12px;
  list-style: disc;
}

.wrapper {
  margin: 40px;
}

.board {
  width: 300px;
  border: 3px solid #3db1ff;
  flex-wrap: wrap;
  margin: 0 auto;
  display: flex;
  box-shadow: 0 0 100px 1px #3db1ff;
}

.board .cell {
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  cursor: pointer;
  border: 3px solid #3db1ff;
  justify-content: center;
  align-items: center;
  font-size: 48px;
  transition: background-color .25s cubic-bezier(.075, .82, .165, 1);
  display: flex;
}

.cell:hover {
  background-color: rgba(61, 177, 255, .2);
}

.title {
  text-align: center;
  color: #fff;
  margin-top: 0;
  margin-bottom: 10px;
}

.reset-button {
  cursor: pointer;
  color: rgba(13, 3, 34, .68);
  border: 3px solid #08041d;
  border-radius: 15px;
  margin: 0 auto 15px;
  padding: 6px;
  font-size: 14px;
  font-weight: 500;
  transition: color .25s cubic-bezier(.075, .82, .165, 1), background-color .25s cubic-bezier(.075, .82, .165, 1), border-radius .25s cubic-bezier(.075, .82, .165, 1), border .25s cubic-bezier(.075, .82, .165, 1);
  display: block;
}

.reset-button:hover {
  color: #fff;
  background-color: #08041d;
  border: 3px solid #fff;
  border-radius: 15px;
}

.counter-wrapper {
  border: 3px solid #fff;
  border-radius: 15px;
  flex-direction: column;
  align-items: center;
  padding: 14px;
  display: flex;
  position: absolute;
}

.counter-title {
  margin-bottom: 5px;
  font-size: 18px;
  text-decoration: underline;
}

.counter-text {
  font-size: 12px;
}

.counter {
  color: #3db1ff;
}

.counter-item:not(:last-child), .total {
  margin-bottom: 10px;
}

.winning-cell {
  color: #d7e3e3;
  background-color: rgba(15, 98, 61, .56);
}

.winning-cell:hover {
  background-color: rgba(19, 157, 95, .54);
}

.button-cover:before {
  color: #d7e3e3;
  padding: 5px;
  font-size: 12px;
  line-height: 1;
}

.button-cover, .knobs, .layer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.button {
  width: 36px;
  height: 17px;
  margin: -20px auto 0;
  position: relative;
  top: 50%;
  overflow: hidden;
}

.button.r, .button.r .layer {
  border-radius: 100px;
}

.knobs {
  z-index: 2;
}

.layer {
  width: 100%;
  z-index: 1;
  background-color: #ebf7fc;
  transition: all .3s;
}

.checkbox {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 3;
  margin: 0;
  padding: 0;
  position: relative;
}

#button-1 .knobs:before {
  content: "ON";
  width: 10px;
  height: 5px;
  color: #fff;
  text-align: center;
  background-color: #200852;
  border-radius: 50%;
  padding: 4px 2px;
  font-size: 5px;
  font-weight: bold;
  line-height: 1;
  transition: all .3s cubic-bezier(.18, .89, .35, 1.15);
  position: absolute;
  top: 2px;
  left: 4px;
}

#button-1 .checkbox:checked + .knobs:before {
  content: "OFF";
  background-color: #8c130a;
  left: 18px;
}

#button-1 .checkbox:checked ~ .layer {
  background-color: #fcebeb;
}

#button-1 .knobs, #button-1 .knobs:before, #button-1 .layer {
  transition: all .3s;
}

.sound-wrapper {
  flex-direction: column-reverse;
  gap: 10px;
  display: flex;
  position: absolute;
  top: 82px;
  right: 60px;
}

.sound-title {
  font-size: 10px;
  position: absolute;
  top: -32px;
  right: -31px;
}

